.product{
  height: 500px;
  max-width: 300px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-weight: 500;
  margin: 1rem 0;
  cursor: pointer;

  img{
    max-height: 300px;
  }

  &-name{
    font-size: 1.4rem;
  }

  &-price{
    font-weight: 400;
  }

  @media screen and (max-width: $tablet-width) {
    max-height: 300px;
    max-width: 180px;

    img{
      height: 200px;
    }
  }
}